import Block from '@/components/block.vue'
export default {
	components: {
		Block
	},
	data() {
		return {
			imgs: [],
			answerDatas: []
		}
	},
	onLoad() {
		this.getImgs()
	},
	onShow() {
		this.getAnswer()
	},
	methods: {
		// 获取轮播图片
		async getImgs() {
			const { data: res } = await this.$myRequest({
				url: '/api/carousel'
			})
			this.imgs = res.data
		},
		
		// 跳转到问题详情页
		navTo() {
			uni.navigateTo({
				url: '/pages/answer/info'
			})
		},
		
		// 跳转到提问页面
		navToAnswer(type) {
			uni.navigateTo({
				url: '/pages/answer/answer?type='+type
			})
		},
		
		// 获取提问列表
		async getAnswer() {
			const { data: res } = await this.$myRequest({
				url: '/api'
			})
			this.answerDatas = res.data
			console.log(res)
		}
	}
}
